.form-control{
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    outline: 0;
    box-shadow: none;
    border: none;
    border-bottom: 2px solid #ced4da;
    border-radius: 0;
    box-sizing: content-box;
    background-color: transparent;
    margin: 0 0 .5rem 0;
    padding: .3rem 0 .55rem 0;
    height: auto;
    display: block;
    width: 100%;
    font-size: 1rem;
    background-image: none;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-clip: padding-box;
    overflow: visible;
    font-family: inherit;
    -webkit-appearance: textfield;
    -webkit-rtl-ordering: logical;
    cursor: text;
    text-rendering: auto;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: start;
    
}

.form-control:focus{
    box-shadow: 0 2px 0 0 #4285f4;
    border-bottom: #4285f4;
    background: 0 0;
    border: 0;
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    outline: 0;
    box-sizing: content-box;
    margin: 0 0 .5rem 0;
    padding: .3rem 0 .55rem 0;
